.slider-toggles {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60px;
  margin: 0;
  padding: 0;
}
.slider-toggles__toggle {
  margin: 0;
  padding: 0;
  width: 6px;
  height: 6px;
  font-size: 0;
  background-color: #283645;
  opacity: 0.3;
  border-radius: 50%;
  border: none;
}
.slider-toggles__toggle:hover {
  opacity: 0.6;
}
.slider-toggles__toggle:active {
  opacity: 0.1;
}
.slider-toggles__toggle--active {
  width: 12px;
  height: 12px;
  box-sizing: border-box;
  background-color: #ffffff;
  border: 2px solid #283645;
  opacity: inherit;
}
.slider__toggle--tariffs {
  margin: 0 auto;
}
.about-app__download-link {
  width: 100%;
