.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 20px;
  position: relative;
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid transparent;
}
.navigation__nav-active {
  background-color: #283645;
  border-bottom: 1px solid #000000;
}
.navigation__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.navigation__menu {
  display: flex;
  flex-direction: column;
}
.navigation__toggle {
  width: 50px;
  height: 24px;
  padding: 0;
  margin: auto;
  font-size: 0;
  background-color: rgba(0, 0, 0, 0.01);
  border: none;
  cursor: pointer;
}
.toggle-open {
  fill: #ffffff;
}
.toggle-open:hover {
  fill: #d22856;
}
.toggle-open:active {
  fill: #d22856;
  opacity: 0.3;
}
.toggle-open--invisible {
  display: none;
}
.toggle-close {
  display: none;
}
.toggle-close--visible {
  display: block;
  fill: #ffffff;
  width: 23px;
  height: 23px;
  margin-left: 50%;
}
.toggle-close--visible:hover {
  fill: #d22856;
}
.toggle-close--visible:active {
  fill: #d22856;
  opacity: 0.3;
}
.navigation__items {
  display: none;
}
.navigation__items--visible {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 63px;
  left: 0;
  width: 100%;
  z-index: 3;
}
.navigation__menu-link {
  box-sizing: border-box;
  padding: 15px 20px;
  min-height: 67px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 32px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
  background: #283645;
}
.navigation__menu-link:active {
  color: #5f3650;
}
.navigation__menu-link:hover {
  color: #d22856;
}
@media (min-width: 660px) {
  .navigation {
    padding-bottom: 20px;
  }
  .navigation__items--visible {
    top: 109px;
  }
  .navigation__toggle {
    margin-top: 17px;
  }
}
@media (min-width: 960px) {
  .navigation {
    padding: 0 130px 0 130px;
  }
  .navigation__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 940px;
    margin: 0 auto;
  }
  .navigation__toggle {
    display: none;
  }
  .navigation__items {
    display: flex;
    justify-content: space-between;
    width: 623px;
  }
  .navigation__menu {
    padding-left: 50px;
  }
  .navigation__menu-link {
    min-height: auto;
    padding-left: 0;
    padding-right: 0;
    font-size: 18px;
    font-weight: normal;
    line-height: 24px;
    text-transform: initial;
    background-color: transparent;
    border: none;
  }
  .navigation__menu-link:active {
    color: #ffffff;
    opacity: 30%;
  }
  .navigation__menu-link--active {
    align-self: center;
    padding: 9px 0;
    border-bottom: 2px solid #ffffff;
  }
}
